import { AxisLeft, AxisBottom } from '@visx/axis';
import { Grid } from '@visx/grid';
import { Group } from '@visx/group'
import { Circle, Line } from '@visx/shape'
import { maxBy, minBy } from 'lodash';
import React, { useMemo } from 'react'
import { format } from 'd3-format'
import { useTooltip, useTooltipInPortal, defaultStyles } from '@visx/tooltip';
import { localPoint } from '@visx/event';
import { HStack, Stack } from '@chakra-ui/layout';

import Text from '../Text'
import Box from '../Box'

const defaultMargin = { top: 40, right: 10, bottom: 30, left: 40 };
const tooltipStyles = {
  ...defaultStyles,
  padding: 12,
  minWidth: 60,
  backgroundColor: 'rgba(0,0,0,0.9)',
  color: 'white',
};

const getExtent = (data, accessor) => {
  const max = maxBy(data, accessor)
  const min = minBy(data, accessor)
  if (typeof accessor === 'string') return [min[accessor], max[accessor]]
  return [accessor(min), accessor(max)]
}

const commaFormat = format(',')
const commaSignedFormat = format('+,')
let tooltipTimeout

const ScatterLink = ({
  width,
  height,
  margin = defaultMargin,
  xScale: xScaleBase,
  yScale: yScaleBase,
  data,
  series,
  highlight,
  ...props
}) => {
  const {
    tooltipOpen,
    tooltipLeft,
    tooltipTop,
    tooltipData,
    hideTooltip,
    showTooltip,
  } = useTooltip();
  const { containerRef, TooltipInPortal } = useTooltipInPortal({
    // TooltipInPortal is rendered in a separate child of <body /> and positioned
    // with page coordinates which should be updated on scroll. consider using
    // Tooltip or TooltipWithBounds if you don't need to render inside a Portal
    scroll: true,
  });

  if (width < 10 || !data || !xScaleBase || !yScaleBase || !series) return null;
  const allData = series.reduce((all, { x, y }) => all.concat(data.map(d => ({ x: x(d), y: y(d) }))), [])
  // bounds
  const xMax = width - margin.left - margin.right;
  const yMax = height - margin.top - margin.bottom;
  const xScale = xScaleBase({
    domain: getExtent(allData, 'x'), // x-coordinate data values
    range: [margin.left, xMax], // svg x-coordinates, svg x-coordinates increase left to right
  });
  const yScale = yScaleBase({
    domain: getExtent(allData, 'y'), // y-coordinate data values
    range: [yMax + margin.top, margin.top], // svg y-coordinates, svg y-coordinates increase left to right
  });

  return (
    <Box as="svg" width={width} height={height} ref={containerRef} {...props}>
       <Grid
        top={margin.top}
        left={margin.left}
        xScale={xScale}
        yScale={yScale}
        width={xMax}
        height={yMax}
        stroke="black"
        strokeOpacity={0.1}
      />
      <AxisBottom
        top={margin.top + yScale(0)}
        left={margin.left}
        scale={xScale}
        tickFormat={(v) => {
          const asString = `${v}`;
          // label only major ticks
          return asString.match(/^[.01?[\]]*$/) ? commaFormat(asString) : '';
        }}
      />
      <AxisLeft
        top={margin.top}
        left={margin.left}
        scale={yScale}
      />
      <Group>
        {data.map((d, j) => {
          const x1 = series[0].x(d)
          const x2 = series[1].x(d)
          const y1 = series[0].y(d)
          const y2 = series[1].y(d)
          return (
            <Line
              key={`line-${j}`}
              className="line"
              from={{
                x: xScale(x1),
                y: yScale(y1),
              }}
              to={{
                x: xScale(x2),
                y: yScale(y2),
              }}
              stroke={x2 < x1 ? 'green' : 'red'}
              strokeWidth={6}
              opacity={tooltipOpen && d.id !== tooltipData.id ? 0.05 : (highlight[d.industry] ? 0.75 : 0.2)}
              onMouseLeave={() => {
                tooltipTimeout = window.setTimeout(() => {
                  hideTooltip();
                }, 300);
              }}
              onMouseMove={event => {
                if (tooltipTimeout) clearTimeout(tooltipTimeout);
                // TooltipInPortal expects coordinates to be relative to containerRef
                // localPoint returns coordinates relative to the nearest SVG, which
                // is what containerRef is set to in this example.
                const eventSvgCoords = localPoint(event);
                showTooltip({
                  tooltipData: { ...d, diff: x2 - x1 },
                  tooltipTop: eventSvgCoords?.y,
                  tooltipLeft: eventSvgCoords?.x,
                });
              }}
            />
          )
        })}
      </Group>
      <Group>
        {data.map((d, j) => (
          <Circle
            key={`point-${j}`}
            className="dot"
            cx={xScale(series[1].x(d))}
            cy={yScale(series[1].y(d))}
            fill="black"
            opacity={tooltipOpen && d.id !== tooltipData.id ? 0.05 : 0.5}
            r={4}
          />
        ))}
      </Group>
      {tooltipOpen && tooltipData && (
        <TooltipInPortal top={tooltipTop} left={tooltipLeft} style={tooltipStyles}>
          <Stack>
            <Text fontWeight="bold" fontSize="0.875em">
              {tooltipData.industry}
            </Text>
            <Text fontSize="1.125em">
              {tooltipData.item}
            </Text>
            <HStack py="0.5em" spacing="1em">
              {series.map(({ x, label }, i)=> (
                <Stack key={i}>
                  <Text>{label}:</Text>
                  <Text fontWeight="bold" fontSize="1.125em">{commaFormat(x(tooltipData))}</Text>
                </Stack>
              ))}
            </HStack>
            <Text
              fontWeight="bold"
              fontSize="1.125em"
              color={tooltipData.diff < 0 ? 'green.500' : 'red.500'}
            >
              {commaSignedFormat(tooltipData.diff)}
            </Text>
          </Stack>
        </TooltipInPortal>
      )}
    </Box>
  )
}

export default ScatterLink
