import React, { useEffect, useMemo, useState } from 'react'
import { csv } from 'd3-fetch'
import { scaleLinear, scaleLog } from '@visx/scale'
import { compact, groupBy, map, mapValues, uniq } from 'lodash'
import { Checkbox, CheckboxGroup, Circle, Flex, Heading, HStack } from "@chakra-ui/react"

import srcData from './data.csv'
import ScatterLink from '../../components/Charts/ScatterLink'
import Sizer from '../../components/Charts/Sizer'
import Box from '../../components/Box'
import Text from '../../components/Text'

const highlight = {
  ICT產業: true,
  非ICT產業: true,
}

const GrowthScatter = () => {
  const [data, setData] = useState()
  const [groups, setGroups] = useState([])
  const [activeGroup, setActiveGroup] = useState([])
  useEffect(() => {
    csv(srcData, (d) => ({
      ...mapValues(d, v => {
        const n = Number(v)
        return isNaN(n) ? v : n
      }),
      id: d.industry + d.item,
    })).then(d => {
      const com = compact(d)
      setData(com)
      setGroups(map(groupBy(com, 'industry'), (items, indu) => [indu, items.length]))
    })
  }, [])
  const filteredData = useMemo(() => {
    if (data) {
      console.log(activeGroup.length)
      return data && data.filter(d => d['2020Q1'] > 0 && d['2021Q1'] > 0 && (activeGroup.length ? activeGroup.includes(d.industry) : true))
    }
    return data
  }, [data, activeGroup])
  return (
    <Box p="2em">
      <Heading mb="0.5em">2021 Q1貿易額變化</Heading>
      <CheckboxGroup onChange={setActiveGroup}>
      <Flex flexWrap="wrap" mx="-0.25em">
        {groups.map(([g, count]) => (
          <Box key={g} m="0.25em">
            <Checkbox value={g}>{g} ({count})</Checkbox>
          </Box>
        ))}
      </Flex>
      </CheckboxGroup>
      <Sizer ratio={21 / 7}>
        <ScatterLink
          data={filteredData}
          xScale={scaleLog}
          yScale={scaleLinear}
          series={[
            { x: d => d['2020Q1'], y: d => d['gr_2020Q1'], label: '2020 Q1' },
            { x: d => d['2021Q1'], y: d => d['gr_2021Q1'], label: '2021 Q1' },
          ]}
          highlight={highlight}
        />
      </Sizer>
    </Box>
  )
}

export default GrowthScatter
